.nav {
  display: flex;
  flex-grow: 1;
  align-self: start;
  align-items: stretch;
  padding-bottom: 50px;
  overflow-x: scroll;
  -webkit-overflow-scrolling: touch;
  margin-right: -1 * spacing(6);
}

.nav-link {
  padding: spacing(5) spacing(6);
  line-height: spacing(6);
  color: $white;
  text-decoration: none;
  border-left: 1px solid color('neutral', 30);
  transition: background-color 0.1s ease-in-out;
}

.nav-link:hover {
  background-color: transparentize(color('neutral', 20), 0.5);
}

.nav-link:active {
  background-color: transparentize(color('neutral', 20), 0.25);
}

.nav-link.is-active {
  background-color: color('neutral', 20);
  //border-color: #489ec7;
}

